body {
  background-image: url('background.jpg');
  background-size: cover;
}

#svHeadline {
  font-size: 5rem;
  text-align: center;
  margin: 0;
  padding: 1rem;
  color: white;
  font-family: fantasy;
  letter-spacing: 4mm;
}

h1{
  font-size: 3rem;
  text-align: center;
  margin: 0;
  padding: 1rem;
  color: rgba(0, 0, 0, 0.535);
  font-family: cursive;
}

.mainDiv {
  margin-top: 1%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

input[type='text'] {
  padding: 1rem;
  font-size: 1rem;
  font-family: cursive;
  border-radius: 0.5rem;
  border: none;
  margin-bottom: 1rem;
}

button {
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  border: none;
  font-size: 1rem;
  color: #ffffff;
  background-color: #ff8c00;
  cursor: pointer;
}

button:hover {
  background-color: #ffa500;
}

.productDiv {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-wrap: wrap;
}

select{
  padding: 1rem 2rem;
  border-radius: 0.5rem;
  border: none;
  font-size: 1rem;
  color: black;
  background-color: #ff8c0000;
  margin: 1rem;
  cursor: pointer;
}

.listDiv{
  width: 250px;
  margin-bottom: 1%;
  border: 5px solid black;
  text-align: center;
}

.listDiv:hover{
background-color: green;
cursor: pointer;
}

h2{
  margin: 0%;
}